.menu {
	overflow-y: scroll;
	list-style: none;
	position: fixed;
	top: 0;
	background:  rgb(253, 208, 255);
	left: 0;
	bottom: 0;
	height: 100vh;
	width: 0;
	overflow: hidden;
	max-width: 290px;
	z-index: 9;
}
  
.menu.showmenu {
	width: 100%;
}
  
li {
	display: block;
	padding: 10px 40px;
	text-decoration: none;
	color:rgb(221, 0, 184);
	text-transform: uppercase;
	font-weight: bold;
	font-family: 'Otomanopee One', sans-serif;
}
  
.menu li:first-child {
	margin-top: 7rem;
}
