.card {
    width: 15rem;
    margin: 1rem;;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 1px solid rgb(221, 0, 184);
    border-radius: 10px;
    background-color: rgb(252, 152, 227);
    color: rgb(108, 3, 139);

}

.card img {
    width: 100%;
    border-radius: 10px;

}

.card-descr {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    padding-bottom: 0.5rem;
}

svg {
    width: 17px;
    color: rgb(108, 3, 139);
}

.card button {
    background: transparent;
    border: 0;
    padding-top: 7px;
} 